import React, {useState, useEffect} from 'react'
import {obtenerProductos} from '../services/productoService'
import { DataGrid } from '@material-ui/data-grid'
import { Link } from "react-router-dom";
export default function DashboardView() {
    const[productos, setProductos] = useState([])

    const getProducts = async () => {
        let response = await obtenerProductos()
        let productosConId = response.map((prod)=>{
            return {...prod, id:prod._id}
        })
        setProductos(productosConId)
    }

    useEffect(()=>{
        getProducts()
    },[])

    //Aqui va las columnas de DataGrid
    let columns=[
        {field: 'nombre', headerName: 'Nombre', width: 400},
        {field: 'descripcion', headerName: 'Descripcion' , width: 400},
        {field: 'precio' , headerName: 'Precio', width: 200},
        
        {
            field: '_id',
            width:150,
            headerName: 'Acciones',
            renderCell: (params)=>(
                //Aqui se puede usar JSX
                <div>
                <Link className="btn btn-info mx-2" to={`/editarproducto/${params.value}`}><i className="fas fa-pencil-alt"></i></Link>
                <Link className="btn btn-danger mx-2" to={`/eliminarproducto/${params.value}`}><i className="fas fa-trash"></i></Link>
                </div>
            )
        }
    ]

    return (
        <div className="mt-3">
      <h1 className="display-4">
        Dashboard
      </h1>
      <div className="row">
        <div className="col-sm-12 col-md-6 col-xl-4">
          <div className="d-grid">
            <button className="btn btn-primary btn-lg">
              Crear Producto
            </button>
          </div>
        </div>
        {/*Para usar datatable de material ux importar:npm install @material-ui/data-grid, npm install @material-ui/data-grid  */}
        <div style={{width:'100%', height:'600px'}}>
            <DataGrid rows={productos} columns={columns} pageSize={8}/>
        </div>
        
        {/*<div className="col-12">
          {/* Mostrando Productos 
          <table className="table">
            <thead>
              <tr>
                <th>Nombre</th>
                <th>Descripcion</th>
                <th>Precio</th>
                <th>Opciones</th>
              </tr>
            </thead>
            <tbody>
              {productos.map((prod, i) => (//i= indice
                <tr key={i}>
                  <td>{prod.nombre}</td>
                  <td>{prod.descripcion}</td>
                  <td>{prod.precio}</td>
                  <td></td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>*/}
      </div>
    </div>
  )
}
